/**
 * API
 * 
 */

import axios from 'axios';

// axios
const downloadApi = axios.create({
  baseURL: '/api/download',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json'
  }
});

// 
downloadApi.interceptors.response.use(
  response => response,
  error => {
    console.error('Download API Error:', error);
    return Promise.reject(error);
  }
);

/**
 * 
 * @param {string} shareCode 
 * @param {string} password 
 * @returns {Promise} API
 */
export async function getShareFileInfo(shareCode, password = '') {
  try {
    const response = await downloadApi.post('/download-api.php', {
      share_code: shareCode,
      password: password,
      action: 'get_info'
    });
    return response.data;
  } catch (error) {
    if (error.response) {
      return error.response.data;
    }
    throw new Error('');
  }
}

/**
 * Token
 * @param {string} shareCode 
 * @param {string} password 
 * @param {string} filePath 
 * @returns {Promise} API
 */
export async function getDownloadToken(shareCode, password = '', filePath = '') {
  try {
    const requestBody = {
      share_code: shareCode,
      password: password,
      action: 'get_download_token'
    };
    
    //  filePath 
    if (filePath && filePath.trim()) {
      requestBody.file_path = filePath;
    }
    
    const response = await downloadApi.post('/download-api.php', requestBody);
    return response.data;
  } catch (error) {
    if (error.response) {
      return error.response.data;
    }
    throw new Error('');
  }
}

/**
 * Token
 * @param {string} token Token
 * @param {boolean} forceDownload 
 * @returns {string} URL
 */
export function downloadByToken(token, forceDownload = true) {
  const params = new URLSearchParams({
    token: token,
    force_download: forceDownload.toString(),
    action: 'download_by_token'
  });
  
  return `/api/download/download-api.php?${params.toString()}`;
}

/**
 * Token
 * @param {string} shareCode 
 * @param {string} password 
 * @param {string} filePath 
 * @param {boolean} forceDownload 
 * @returns {Promise<string>} URL Promise
 */
export async function downloadShareFile(shareCode, password = '', filePath = '', forceDownload = true) {
  // 1. Token
  const tokenResponse = await getDownloadToken(shareCode, password, filePath);
  
  if (!tokenResponse.success) {
    throw new Error(tokenResponse.message || 'Token');
  }
  
  // 2. TokenURL
  return downloadByToken(tokenResponse.data.token, forceDownload);
}

/**
 *  - URL
 * @param {string} shareCode 
 * @param {string} password 
 * @param {boolean} forceDownload 
 * @returns {string} URL
 */
export function downloadShareFileCompat(shareCode, password = '', forceDownload = true) {
  const params = new URLSearchParams({
    share_code: shareCode,
    password: password,
    action: 'download',
    force_download: forceDownload.toString()
  });
  
  return `/api/download/download-api.php?${params.toString()}`;
}

/**
 * 
 * @param {number} bytes 
 * @param {number} precision 
 * @returns {string} 
 */
export function formatFileSize(bytes, precision = 2) {
  if (!bytes || bytes === 0) return '0 B';
  
  const units = ['B', 'KB', 'MB', 'GB', 'TB'];
  let size = bytes;
  let unitIndex = 0;
  
  while (size >= 1024 && unitIndex < units.length - 1) {
    size /= 1024;
    unitIndex++;
  }
  
  return `${size.toFixed(precision)} ${units[unitIndex]}`;
}

/**
 * 
 * @param {string} fileType 
 * @returns {string} 
 */
export function getFileTypeIcon(fileType) {
  const iconMap = {
    // 
    'jpg': 'fas fa-image',
    'jpeg': 'fas fa-image', 
    'png': 'fas fa-image',
    'gif': 'fas fa-image',
    'webp': 'fas fa-image',
    'svg': 'fas fa-image',
    
    // 
    'pdf': 'fas fa-file-pdf',
    'doc': 'fas fa-file-word',
    'docx': 'fas fa-file-word',
    'xls': 'fas fa-file-excel',
    'xlsx': 'fas fa-file-excel',
    'ppt': 'fas fa-file-powerpoint',
    'pptx': 'fas fa-file-powerpoint',
    'txt': 'fas fa-file-alt',
    
    // 
    'mp3': 'fas fa-file-audio',
    'wav': 'fas fa-file-audio',
    'mp4': 'fas fa-file-video',
    'avi': 'fas fa-file-video',
    'mkv': 'fas fa-file-video',
    
    // 
    'zip': 'fas fa-file-archive',
    'rar': 'fas fa-file-archive',
    '7z': 'fas fa-file-archive',
    
    // 
    'html': 'fas fa-file-code',
    'css': 'fas fa-file-code',
    'js': 'fas fa-file-code',
    'php': 'fas fa-file-code',
    'py': 'fas fa-file-code'
  };
  
  return iconMap[fileType?.toLowerCase()] || 'fas fa-file';
}

export default {
  getShareFileInfo,
  getDownloadToken,
  downloadByToken,
  downloadShareFile,
  downloadShareFileCompat,
  formatFileSize,
  getFileTypeIcon
};
