<template>
  <div class="file-list-container">
    <div class="down-main-card">
      <div class="card-header">
        <h3><i class="fas fa-folder-open"></i> </h3>
        <div v-if="shareCode" class="share-code-display">
          <code>{{ shareCode }}</code>
        </div>
      </div>
      
      <div class="card-body">
        <!--  -->
        <div v-if="!shareCode" class="share-code-section">
          <label for="shareCodeInput" class="input-label"></label>
          <div class="input-group">
            <input
              id="shareCodeInput"
              v-model="inputShareCode"
              type="text"
              class="input-box"
              placeholder="8"
              maxlength="8"
              @keyup.enter="loadShareInfo"
            />
            <button 
              class="modal-btn primary" 
              @click="loadShareInfo" 
              :disabled="!inputShareCode.trim() || loading"
            >
              <i class="fas fa-search" v-if="!loading"></i>
              <i class="fas fa-spinner fa-spin" v-else></i>
              {{ loading ? '...' : '' }}
            </button>
          </div>
        </div>

        <!--  -->
        <div v-if="needPassword" class="password-section">
          <label for="accessPassword" class="input-label">
            <i class="fas fa-lock"></i> 
          </label>
          <div class="input-group">
            <input
              id="accessPassword"
              v-model="accessPassword"
              type="password"
              class="input-box"
              placeholder=""
              @keyup.enter="verifyPassword"
            />
            <button 
              class="modal-btn primary" 
              @click="verifyPassword" 
              :disabled="!accessPassword.trim() || loading"
            >
              
            </button>
          </div>
        </div>

        <!--  -->
        <div v-if="errorMessage" class="error-message">
          <i class="fas fa-exclamation-triangle"></i>
          {{ errorMessage }}
        </div>

        <!--  -->
        <div v-if="fileInfo && !needPassword" class="file-info-section">
          <!--  -->
          <div class="file-basic-info">
            <div class="file-icon">
              <i :class="getFileTypeIcon(fileInfo.file_type)"></i>
            </div>
            <div class="file-details">
              <h4 class="file-name">{{ fileInfo.file_name }}</h4>
              <div class="file-meta">
                <span class="file-size">{{ formatFileSize(fileInfo.file_size) }}</span>
                <span class="file-type">{{ fileInfo.file_type?.toUpperCase() || 'FILE' }}</span>
                <span class="share-name">{{ fileInfo.share_name }}</span>
              </div>
              <div class="file-stats">
                <span><i class="fas fa-eye"></i> {{ fileInfo.view_count }} </span>
                <span><i class="fas fa-download"></i> {{ fileInfo.download_count }} </span>
              </div>
            </div>
          </div>

          <!--  -->
          <div class="download-actions">
            <button class="modal-btn primary download-btn" @click="downloadFile">
              <i class="fas fa-download"></i>
              
            </button>
            <!--  -->
            <a 
              v-if="extensionData.btn && extensionData.url" 
              :href="extensionData.url" 
              target="_blank"
              class="modal-btn secondary extension-button"
            >
              <i class="fas fa-external-link-alt"></i>
              {{ extensionData.btn }}
            </a>
          </div>

          <!--  -->
          <div v-if="extensionData.image" class="extension-section">
            <div class="extension-preview">
              <h5><i class="fas fa-gift"></i> </h5>
              <div class="extension-image-container">
                <img 
                  :src="extensionData.image"
                  alt=""
                  class="extension-image"
                  @error="hideExtensionImage"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, defineExpose } from 'vue';
import { useRoute } from 'vue-router';
import { 
  getShareFileInfo, 
  downloadShareFile,
  formatFileSize,
  getFileTypeIcon
} from './downloadApi.js';

// 
const route = useRoute();

// 
const inputShareCode = ref('');
const shareCode = ref('');
const accessPassword = ref('');
const needPassword = ref(false);
const loading = ref(false);
const errorMessage = ref('');
const fileInfo = ref(null);
const extensionData = ref({ btn: null, url: null, image: null });



// 
const clearError = () => {
  errorMessage.value = '';
};

const setError = (message) => {
  errorMessage.value = message;
  setTimeout(() => {
    errorMessage.value = '';
  }, 5000); // 5
};

const loadShareInfo = async () => {
  if (!inputShareCode.value.trim()) {
    setError('');
    return;
  }

  if (inputShareCode.value.length !== 8) {
    setError('8');
    return;
  }

  clearError();
  loading.value = true;

  try {
    const response = await getShareFileInfo(inputShareCode.value.trim());
    
    if (response.success) {
      shareCode.value = inputShareCode.value.trim();
      fileInfo.value = response.data;
      needPassword.value = false;
      
      // 
      if (response.data.extension_data) {
        extensionData.value = {
          btn: response.data.extension_data.btn || null,
          url: response.data.extension_data.url || null,
          image: response.data.extension_data.image || null
        };
      } else {
        extensionData.value = { btn: null, url: null, image: null };
      }
      
    } else {
      if (response.message === '') {
        shareCode.value = inputShareCode.value.trim();
        needPassword.value = true;
        if (response.data) {
          fileInfo.value = { share_name: response.data.share_name };
        }
      } else {
        setError(response.message || '');
      }
    }
  } catch (error) {
    console.error(':', error);
    setError('');
  } finally {
    loading.value = false;
  }
};

const verifyPassword = async () => {
  if (!accessPassword.value.trim()) {
    setError('');
    return;
  }

  clearError();
  loading.value = true;

  try {
    const response = await getShareFileInfo(shareCode.value, accessPassword.value);
    
    if (response.success) {
      fileInfo.value = response.data;
      needPassword.value = false;
      
      // 
      if (response.data.extension_data) {
        extensionData.value = {
          btn: response.data.extension_data.btn || null,
          url: response.data.extension_data.url || null,
          image: response.data.extension_data.image || null
        };
      } else {
        extensionData.value = { btn: null, url: null, image: null };
      }
      
    } else {
      setError(response.message || '');
    }
  } catch (error) {
    console.error(':', error);
    setError('');
  } finally {
    loading.value = false;
  }
};

const downloadFile = async () => {
  if (!shareCode.value) return;
  
  try {
    const downloadUrl = await downloadShareFile(shareCode.value, accessPassword.value, '', true);
    
    // 
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.style.display = 'none';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  } catch (error) {
    console.error(':', error);
    alert(': ' + error.message);
  }
};


const hideExtensionImage = () => {
  // 
  console.warn('');
};

// 
onMounted(() => {
  // URL ()
  const urlShareCode = route.params.shareCode || route.query.code;
  if (urlShareCode && urlShareCode.length === 8) {
    inputShareCode.value = urlShareCode;
    loadShareInfo();
  }
});

// 
defineExpose({
  loadShareInfo,
  verifyPassword,
  downloadFile
});
</script>

<style scoped>
@import './down-main.css';
</style>
